@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  width: 100%;
}

h1 {
  font-size: 1.5rem;
}

body {
  font-family: "Red Hat Display", sans-serif;
  background: #e0e8ff url("../../../images/pattern-background-mobile.svg") no-repeat;
  background-size: contain;
}
@media (min-width: 40em) {
  body {
    background: #e0e8ff url("../../../images/pattern-background-desktop.svg") no-repeat;
    background-size: contain;
  }
}

.container {
  padding: 0 1.5625rem;
  /* 	max-width: rem(1100); */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
}

.attribution {
  font-size: 0.6875rem;
  text-align: center;
  color: #7280a7;
}
.attribution a {
  color: #3829e0;
}

.card {
  margin: 2rem 0 2rem 0;
  max-width: 25rem;
  background: #fff;
  border-radius: 1rem;
  text-align: center;
}
.card__description {
  color: #7280a7;
  line-height: 1.375rem;
  font-size: 0.875rem;
}
.card__plan {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f5f7ff;
  padding: 10px 20px;
  border-radius: 20px;
}
.card__image img {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.card__content {
  padding-left: 2rem;
  padding-right: 2rem;
}
.card__footer {
  padding: 2rem;
}
.card__icon {
  width: 3rem;
}

.btn {
  display: inline-flex;
  border-radius: 0.8rem;
  padding: 1rem 1rem;
  outline: none;
  border: none;
  transition: 0.23s ease-in-out;
  width: 100%;
  justify-content: center;
}
.btn--primary {
  background: #3829e0;
  color: #f5f7ff;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}
.btn--link {
  background: none;
  color: #7280a7;
  font-weight: 700;
}